.menu {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  z-index: var(--z-fixed);
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 0;
  // animation: show .3s ease-in-out;
  transition: all .3s ease-in-out;

  &__inner {
    max-width: 375px;
    background: var(--gradient);
    padding: 60px 60px 20px 37px;
    flex-direction: column;
    justify-content: space-between;
    display: flex;
    width: 100%;
    box-shadow: 3px 0px 10px rgba(0, 0, 0, 0.4);
    overflow-y: auto;
    position: relative;
    left: -100%;
    transition: all .3s ease-in-out;
    // animation: right .3s ease-in-out;
    height: 100%;
    @media screen and (max-width: 500px) {
      max-width: 80vw;
      padding: 10px;
    }
  }

  & *{
    color: white;
  }

  & &__list {
    padding-left: 23px;
    @media screen and (max-width: 500px) {
      padding-left: 0;
    }
    .menu__item-link {
      width: fit-content;
      &::after {
        content: '';
        position: relative;
        right: 0px;
        width: 30px;
        height: 30px;
        background-color: var(--first-color-lighter);
        border-radius: 50%;
        transition: right .3s ease-in-out, opacity .3s ease-in-out;
        opacity: 0;
      }
      &:hover {
        &::after {
          right: -60px;
          opacity: 1;
        }
      }
      &.active {
        // color: var(--first-color-lighter);
        background: var(--first-color-lighter);
        &::after {
          right: -60px;
          opacity: 1;
        }
      }
    }
  }

  &.show {
    display: flex;
    opacity: 1;
    .menu__inner {
      left: 0;
    }
  }
  
  &.hidden {
    visibility: hidden;
  }
}

@keyframes show {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes right {
  0% {
    left: -100%;
  }
  100% {
    left: 0;
  }
}
