@import url(https://fonts.googleapis.com/css?family=Poppins:100,100italic,200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic,800,800italic,900,900italic);
.card{width:100%;height:268px;background-color:white;box-shadow:0px 4px 4px rgba(0,0,0,0.25);display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}

.menu__item{margin-bottom:var(--mb-1-5);width:100%;position:relative}.menu__item:last-child{margin-bottom:0}.menu__item-link{display:-webkit-flex;display:flex;font-size:var(--h3-font-size);color:black;font-weight:var(--font-medium);padding:10px;border-radius:50px;width:100%}.menu__item-card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background:linear-gradient(180deg, var(--first-color), var(--first-color-lighter) 45%, #fff 0, #fff);transition:.4s ease-in-out}.menu__item-card img{width:170px;height:170px;border-radius:50%;margin-bottom:15px;border:10px solid white}


.switch{position:relative;display:inline-block;width:71px;height:34px;display:-webkit-flex;display:flex}.switch input{display:none}.slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:var(--text-color-light);transition:.4s;text-align:right;font-size:var(--smaller-font-size);line-height:34px}.slider span{position:absolute;display:block;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:100%;right:10px;left:auto;left:initial;z-index:1;color:#FFF}.slider:before{position:absolute;content:"";height:22px;width:22px;left:4px;bottom:6px;background-color:white;transition:.4s;z-index:2}input:checked+.slider{background-color:var(--first-color)}input:checked+.slider span{position:absolute;left:10px;right:0}input:focus+.slider{box-shadow:0 0 1px var(--first-color)}input:checked+.slider:before{left:44px}.slider.round{border-radius:34px}.slider.round:before{border-radius:50%}

.header .nav{padding-top:48px;padding-bottom:48px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.header .nav__toggle{position:relative;width:45px;height:30px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;cursor:pointer}.header .nav__toggle-item{width:100%;height:5px;background-color:var(--first-color)}.header .nav__btns{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.header .nav__btns .btn{height:100%;padding:0 15px;border:none;outline:none;background-color:var(--first-color);color:#FFF;border-radius:50px;margin:0 5px;cursor:pointer;transition:background .3s ease-in-out;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.header .nav__btns .btn:hover{background-color:var(--first-color-lighter)}.header .nav__btns-start{position:fixed;bottom:10px;right:10px;z-index:var(--z-fixed);background-color:var(--first-color);padding:10px;border-radius:50%;width:60px;height:60px;cursor:pointer;color:#FFF;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;outline:none;border:none;box-shadow:3px 3px 5px rgba(0,0,0,0.15)}.header .nav__btns-start:disabled{background:gray;cursor:auto}.header .nav__btns-start img{width:auto;height:100%;object-fit:contain;padding:5px}

:root{--header-height: 3rem;--hue-color: 250;--first-color: hsl(var(--hue-color), 69%, 61%);--first-color-second: hsl(var(--hue-color), 69%, 61%);--first-color-alt: hsl(var(--hue-color), 57%, 53%);--first-color-lighter: hsl(var(--hue-color), 92%, 85%);--title-color: hsl(var(--hue-color), 8%, 15%);--text-color: hsl(var(--hue-color), 8%, 45%);--text-color-light: hsl(var(--hue-color), 8%, 65%);--input-color: hsl(var(--hue-color), 70%, 96%);--body-color: hsl(var(--hue-color), 60%, 99%);--container-color: #FFF;--scroll-bar-color: hsl(var(--hue-color), 12%, 99%);--scroll-thumb-color: hsl(var(--hue-color), 12%, 80%);--gradient: linear-gradient(180deg,var(--first-color) 0, var(--first-color-lighter) 100%);--body-font: 'Poppins', sans-serif;--big-font-size: 2rem;--h1-font-size: 1.5rem;--h2-font-size: 1.25rem;--h3-font-size: 1.125rem;--normal-font-size: .938rem;--small-font-size: .813rem;--smaller-font-size: .75rem;--font-medium: 500;--font-semi-bold: 600;--mb-0-25: .25rem;--mb-0-5: .5rem;--mb-0-75: .75rem;--mb-1: 1rem;--mb-1-5: 1.5rem;--mb-2: 2rem;--mb-2-5: 2.5rem;--mb-3: 3rem;--z-tooltip: 10;--z-fixed: 100;--z-modal: 1000}@media screen and (min-width: 968px){:root{--big-font-size: 3rem;--h1-font-size: 2.25rem;--h2-font-size: 1.5rem;--h3-font-size: 1.25rem;--normal-font-size: 1rem;--small-font-size: .875rem;--smaller-font-size: .813rem}}body.dark-theme{--first-color-second: hsl(var(--hue-color), 30%, 8%);--title-color: hsl(var(--hue-color), 8%, 95%);--text-color: hsl(var(--hue-color), 8%, 75%);--input-color: hsl(var(--hue-color), 29%, 16%);--body-color: hsl(var(--hue-color), 28%, 12%);--container-color: hsl(var(--hue-color), 29%, 16%);--scroll-bar-color: hsl(var(--hue-color), 12%, 48%);--scroll-thumb-color: hsl(var(--hue-color), 12%, 36%)}*{box-sizing:border-box;padding:0;margin:0}html{scroll-behavior:smooth}body{margin:0 0 3rem 0;margin:0 0 var(--header-height) 0;font-family:'Poppins', sans-serif;font-family:var(--body-font);font-size:.938rem;font-size:var(--normal-font-size);background-color:hsl(250, 60%, 99%);background-color:var(--body-color);color:hsl(250, 8%, 45%);color:var(--text-color)}h1,h2,h3,h4{color:hsl(250, 8%, 15%);color:var(--title-color);font-weight:600;font-weight:var(--font-semi-bold)}ul{list-style:none}a{text-decoration:none}img{width:100%;height:100%;object-fit:cover}.container{max-width:1008px;margin:0 auto;padding-left:1.5rem;padding-left:var(--mb-1-5);padding-right:1.5rem;padding-right:var(--mb-1-5)}.grid{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:28px;gap:28px}@media screen and (max-width: 768px){.grid{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 500px){.grid{grid-template-columns:repeat(1, 1fr)}}.btn{padding:5px 15px;background-color:hsl(250, 69%, 61%);background-color:var(--first-color);border-radius:50px;color:#FFF;border:none;outline:none;transition:.3s ease-in-out;cursor:pointer;font-size:1.125rem;font-size:var(--h3-font-size)}.btn+.btn{margin-left:10px}.btn:hover{background-color:hsl(250, 92%, 85%);background-color:var(--first-color-lighter)}.row{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.row>*{margin:5px}.row--flex-end{-webkit-justify-content:flex-end;justify-content:flex-end}

.checks-bar{display:-webkit-flex;display:flex;width:100%;-webkit-justify-content:flex-end;justify-content:flex-end;padding:15px 0;overflow:hidden;color:var(--first-color);font-size:1.5rem}.checks-bar__item{padding:3px}

.word{background-color:transparent;width:100%;height:268px;-webkit-perspective:1000px;perspective:1000px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around}.word[aria-disabled=true]{pointer-events:none;opacity:0.7}.word:hover>.word__card{-webkit-transform:translateZ(2rem);transform:translateZ(2rem)}.word__card{width:100%;height:100%;position:relative;text-align:center;transition:-webkit-transform 0.8s;transition:transform 0.8s;transition:transform 0.8s, -webkit-transform 0.8s;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}.word.flipped>.word__card{-webkit-transform:rotateY(-180deg);transform:rotateY(-180deg)}.word__front,.word__back{position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.word__front{background-color:white;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;cursor:pointer}.word__front-header{width:100%;height:100%}.word__front-footer{width:100%;min-height:50px;border-top:2px solid var(--first-color);position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.word__front-footer span{font-size:var(--h2-font-size)}.word__front-footer .btn{padding:10px;background-color:var(--first-color);width:40px;height:40px;border-radius:50%;position:absolute;right:10px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);color:white}.word__name{display:block;font-size:var(--small-font-size)}.word__back{background-color:var(--first-color);background-position:center;background-size:cover;-webkit-transform:rotateY(180deg);transform:rotateY(180deg);color:#FFF;font-size:var(--h2-font-size)}

.result-page{max-width:600px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.result-page img{max-width:400px;max-height:400px;object-fit:contain}.result-page span{font-size:var(--h2-font-size);display:block;padding:10px 15px;border-radius:50px;background-color:var(--first-color);color:#FFF;text-align:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:0 auto;margin-top:30px;position:relative}.result-page span::after,.result-page span::before{position:absolute;content:'';top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);width:35px;height:35px;border-radius:50%;background-color:var(--first-color)}.result-page span::after{right:-40%}.result-page span::before{left:-40%}

.menu{position:fixed;left:0;right:0;top:0;bottom:0;height:100%;z-index:var(--z-fixed);background-color:rgba(0,0,0,0.2);opacity:0;transition:all .3s ease-in-out}.menu__inner{max-width:375px;background:var(--gradient);padding:60px 60px 20px 37px;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;display:-webkit-flex;display:flex;width:100%;box-shadow:3px 0px 10px rgba(0,0,0,0.4);overflow-y:auto;position:relative;left:-100%;transition:all .3s ease-in-out;height:100%}@media screen and (max-width: 500px){.menu__inner{max-width:80vw;padding:10px}}.menu *{color:white}.menu .menu__list{padding-left:23px}@media screen and (max-width: 500px){.menu .menu__list{padding-left:0}}.menu .menu__list .menu__item-link{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.menu .menu__list .menu__item-link::after{content:'';position:relative;right:0px;width:30px;height:30px;background-color:var(--first-color-lighter);border-radius:50%;transition:right .3s ease-in-out, opacity .3s ease-in-out;opacity:0}.menu .menu__list .menu__item-link:hover::after{right:-60px;opacity:1}.menu .menu__list .menu__item-link.active{background:var(--first-color-lighter)}.menu .menu__list .menu__item-link.active::after{right:-60px;opacity:1}.menu.show{display:-webkit-flex;display:flex;opacity:1}.menu.show .menu__inner{left:0}.menu.hidden{visibility:hidden}@-webkit-keyframes show{0%{opacity:0}100%{opacity:1}}@keyframes show{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes right{0%{left:-100%}100%{left:0}}@keyframes right{0%{left:-100%}100%{left:0}}

.statistic-table__block{max-width:100%;overflow:auto;margin:0 auto}.statistic-table__block .row{margin-bottom:15px}.statistic-table__block table{width:100%;min-width:830px;border-collapse:collapse}.statistic-table__block table td,.statistic-table__block table th{padding:5px 10px}.statistic-table__block table td{text-align:center}.statistic-table__block table tr td{border-bottom:1px solid var(--first-color)}

th{cursor:pointer;background-color:var(--first-color);color:#FFF;border-radius:50px}th .arrow{border:solid #FFF;border-width:0 3px 3px 0;display:inline-block;padding:3px}th .arrow.up{transform:rotate(-135deg) translateY(2px) translateX(-3px);-webkit-transform:rotate(-135deg) translateY(2px) translateX(-3px)}th .arrow.down{transform:rotate(45deg) translateY(-3px);-webkit-transform:rotate(45deg) translateY(-3px)}

.statistic-page .row{margin-bottom:10px}

