.result-page {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    max-width: 400px;
    max-height: 400px;
    object-fit: contain;
  }
  span {
    font-size: var(--h2-font-size);
    display: block;
    padding: 10px 15px;
    border-radius: 50px;
    background-color: var(--first-color);
    color: #FFF;
    text-align: center;
    width: fit-content;
    margin: 0 auto;
    margin-top: 30px;
    position: relative;
    &::after, &::before {
      position: absolute;
      content: '';
      top: 50%;
      transform: translateY(-50%);
      width: 35px;
      height: 35px;
      border-radius: 50%;
      background-color: var(--first-color);
    }
    &::after {
      right: -40%;
    }
    &::before {
      left: -40%;
    }
  }
}