.header {
  .nav{
    padding-top: 48px;
    padding-bottom: 48px;
    display: flex;
    justify-content: space-between;
    &__toggle {
      position: relative;
      width: 45px;
      height: 30px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      cursor: pointer;
      &-item {
        width: 100%;
        height: 5px;
        background-color: var(--first-color);
      }
    }
    &__btns {
      display: flex;
      align-items: center;
      .btn {
        height: 100%;
        padding: 0 15px;
        border: none;
        outline: none;
        background-color: var(--first-color);
        color: #FFF;
        border-radius: 50px;
        margin: 0 5px;
        cursor: pointer;
        transition: background .3s ease-in-out;
        display: flex;
        align-items: center;
        &:hover {
          background-color: var(--first-color-lighter);
        }
      }
      &-start {
        position: fixed;
        bottom: 10px;
        right: 10px;
        z-index: var(--z-fixed);
        background-color: var(--first-color);
        padding: 10px;
        border-radius: 50%;
        width: 60px;
        height: 60px;
        cursor: pointer;
        color: #FFF;
        display: flex;
        justify-content: center;
        align-items: center;
        outline: none;
        border: none;
        box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.15);
        &:disabled {
          background: gray;
          cursor: auto;
        }
        img{
          width: auto;
          height: 100%;
          object-fit: contain;
          padding: 5px;
        }
      }
    }
  }
}